import React from 'react';
import { Link } from 'react-router-dom';
function Trafic(props) {
    let a = -props.n;
    return (
        <div >
        <h4>Посещаемость сайта</h4>
            <svg xmlns="http://www.w3.org/2000/svg" version="1.1" width="100%" height="400">
                <polyline points={props.pointsArray(props.n , props.arr2)}
                    style={{ fill: 'none', stroke: '#19c7d9', strokeWidth: 3 }} />
                {props.arr2.map((x) => <circle cx={a = a + props.n} cy={x} r="7" stroke="#19c7d9" fill= "#19c7d9"/>)}
                
            </svg>
            <div className="row ">
                {props.months.map((x, i) => <div key={i + 2} className="col-sm-1">{x}</div>)}

            </div>
            
        </div>

    )
}
export default Trafic;