import React, { useState, useEffect } from 'react';
import './css/App.css';
import Menu from './components/Menu';
import {
  Route
} from "react-router-dom";
import Admin from './pages/Admin';
import Main from './pages/Main';
import Menuinsert from './pages/Menuinsert';
import Menuupdate from './pages/Menuupdate';
import { getMenu } from './actions';
import Articlesmain from './pages/Articlesmain';
import Articlesinsert from './pages/Articlesinsert';
import Articlesupdate from './pages/Articlesupdate';
function App() {
  const [menu, setmenu] = useState({ data: [{}], status: 0 });
  useEffect(() => {
    getMenu(setmenu);

  }, []);
  return (
    <div className="App container-fluid p-0" >
      <div className="text-center" style={{ height: '50px', backgroundColor: 'green', color: '#ffffff', fontSize: '18pt' }}>Административная панель</div>

      <div className="row">
        <div className="col-1" style={{ borderRight: 'solid 2px #cccccc', width: '200px' }}>
          <Menu menuid={menu.data[0].menu_art_id} />
        </div>
        <div className="col-sm">






          <Route exact path="/admin/index.php" component={Admin} />
          <Route path="/admin/index.php/menu/:id" component={Main} />
          <Route path="/admin/index.php/menuinsert" component={Menuinsert} />
          <Route path="/admin/index.php/menuupdate/:id" component={Menuupdate} />
          <Route path="/admin/index.php/articles/:id" component={Articlesmain} />
          <Route path="/admin/index.php/articlesinsert" component={Articlesinsert} />
          <Route path="/admin/index.php/articlesupdate/:id" component={Articlesupdate} />
        </div>
      </div>

    </div>
  );
}

export default App;
