import React from 'react';
import { Link } from 'react-router-dom';
function Updatelist(props) {
    return (
        <div className="col-sm">
            <div className="text-right mt-3">
                <Link to={props.updatealias} type="button" className="btn btn-primary" data-toggle="modal" data-target="#exampleModal">Редактировать</Link>
            </div>

            <div className="row mt-3">
                <div className="col-3">
                    Название
                        </div>
                <div className="col">
                    {props.name}
                </div>
            </div>
            <div className="row mt-3">
                <div className="col-3">
                    Url адрес
                        </div>
                <div className="col">
                    {props.alias}
                </div>
            </div>
            <div className="row mt-3">
                <div className="col-3">
                    Заголовок страницы
                        </div>
                <div className="col">
                    {props.title}
                </div>
            </div>
            <div className="row mt-3">
                <div className="col-3 ">
                    Ключевые слова
                        </div>
                <div className="col">
                    {props.keyword}
                </div>
            </div>
            <div className="row mt-3">
                <div className="col-3">
                    Краткое описание
                        </div>
                <div className="col">
                    {props.descript}
                </div>
            </div>
            <div className="row mt-3">
                <div className="col-3">
                    Текст
                        </div>
                <div className="col">
                    {props.content}
                </div>
            </div>
            {(props.img) ?
                <div className="row mt-3">
                    <div className="col-3">
                        Изображение
                        </div>
                    <div className="col">
                        <img className="img-fluid" src={`${props.initalBaseUrl}/img/window/${props.img}`} alt={props.name} />
                    </div>
                </div>
                : <div></div>
            }

        </div>
    )
} export default Updatelist;