import React from 'react';

function Images(props) {
    return (
        <div>
            <h1>Изображение</h1>
            <div className="row">

                {props.array_obj(props.files).map((x, i) =>
                    <div key={i + 10} style={{ border: 'solid 2px green' }} style={(props.img.img === x) ? { border: 'solid 2px red' } : { border: 'solid 2px green' }} className="form-check m-2  col-sm-2 " >
                        <input style={{ marginLeft: '-1rem' }} id={`menu_img_${i}`} className="form-check-input" type="checkbox" value={(props.img.bol === false) ? x : ''} onChange={(e) => props.setimg({ img: e.target.value, bol: e.target.checked })} />
                        <label htmlFor={`menu_img_${i}`} className="form-check-label"><img width="100%" src={`${props.initalBaseUrl}/img/window/${x}`} /></label>
                    </div>
                )}
            </div>
        </div>
    )
} export default Images;