import React, { useState, useEffect } from 'react';
import { insertMenu, initalBaseUrl, getFiles, array_obj, getMenu, getArticles, insertMenuArt } from '../actions';
import { Link } from 'react-router-dom';
import Images from '../components/Images';
import Articles from '../components/Articles';
function Menuinsert() {
    const [menuname, setmenuname] = useState({ data: [{}], status: 0 });
    const [menu, setmenu] = useState({ data: [{}], status: 0 });
    const [files, setfiles] = useState({ data: {}, status: 0 });
    const [articles, setarticles] = useState({ data: {}, status: 0 });
    const [insermenuarticles, setinsermenuarticles] = useState({ data: {}, status: 0 });
    const [save, setsave] = useState('nosave');
    const [names, setnames] = useState('');
    const [menuartid, setmenuartid] = useState('');
    const [alias, setalias] = useState('');
    const [title, settitle] = useState('');
    const [keyword, setkeyword] = useState('');
    const [descript, setdescript] = useState('');
    const [content, setcontent] = useState('');
    const [img, setimg] = useState({ img: '', bol: false });
    const [parent, setparent] = useState(0);
    const [imgart, setimgart] = useState('img');
    const [artarr, setartarr] = useState({ id: 0, c: false });
    const [artarrsd, setartarrsd] = useState([]);


    useEffect(() => {
        getFiles(setfiles);
        getMenu(setmenuname);
        getArticles(setarticles);
    }, []);
    useEffect(() => {
        if (names !== '' && title !== '' && alias !== '') {
            setsave('save');
        }
    }, [names, title, alias]);

    useEffect(() => {
        const option = artarrsd;
        if (artarr.c === true) {
            option.push(artarr.id);
            setartarrsd(option);
        }
        if (artarr.c === false) {
            var pos = option.indexOf(artarr.id);
            option.splice(pos, 1)
            setartarrsd(option);
        }
    }, [artarr.id, artarr.c]);


    function handleSubmit(event) {
        insertMenu(setmenu, save, Number(menuname.data[menuname.data.length - 1].menu_art_id) + 1, names, alias, title, keyword, descript, content, img.img, parent);
        insertMenuArt(setinsermenuarticles, Number(menuname.data[menuname.data.length - 1].menu_art_id) + 1, artarrsd)
        event.preventDefault();


    }

    return (
        <div className="container-fluid" >
            <h2>Создание нового пункта меню</h2>
            <form onSubmit={handleSubmit}>
                <div className="row">
                    <div className="row">
                        <button className="btn btn-primary ml-4 " type="submit">Сохранить</button>
                        <Link className="btn btn-primary ml-3" to={`/admin/index.php/menu/${menuname.data[0].menu_art_id}`}>Закрыть</Link>
                    </div>
                    <div className="col">
                        {(menu.status === 200 && save === 'save') ? <div style={{ color: 'green', fontSize: '15pt' }}>Пункт меню успешно создан</div> : (menu.status === 200 && save !== 'save') ? <div style={{ color: 'red', fontSize: '15pt' }}>Ошибка! Заполните поля с звездочкой.</div> : ''}
                    </div>
                </div>

                <div className="row">
                    <div className="col">
                        <div className="form-group">
                            <label htmlFor="menu_name"><h3>Название *</h3></label>
                            <input type="text" className="form-control" id="menu_name" onChange={(e) => setnames(e.target.value)} />
                        </div>
                    </div>
                    <div className="col">
                        <div className="form-group">
                            <label htmlFor="menu_alias"><h3>Адрес страницы *</h3></label>
                            <input type="text" onChange={(e) => setalias(e.target.value)} className="form-control" id="menu_alias" />
                        </div>
                    </div>
                    <div className="col">
                        <div className="form-group">
                            <label htmlFor="menu_alias"><h3>Заголовок страницы *</h3></label>
                            <input type="text" onChange={(e) => settitle(e.target.value)} className="form-control" id="menu_alias" />
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="col-sm">
                        <label htmlFor="menu_keyword"><h3>Ключевые слова</h3></label>
                        <textarea className="form-control" id="menu_keyword" onChange={(e) => setkeyword(e.target.value)} rows={3} defaultValue={""} />
                        <label htmlFor="menu_descript"><h3>Краткое описание</h3></label>
                        <textarea className="form-control" id="menu_descript" onChange={(e) => setdescript(e.target.value)} rows={3} defaultValue={""} />
                        
                        <Images array_obj={array_obj} setimg={setimg} img={img} initalBaseUrl={initalBaseUrl} files={files.data} />
                    </div>
                    <div className="col-4">
                        <label htmlFor="menu_keyword"><h3>Привязать к пункт меню</h3></label>
                        <select className="form-control form-control-lg" onChange={(e) => setparent(e.target.value)}>
                            <option value={0}>Нет</option>
                            {array_obj(menuname.data).map((x) => <option key={x.menu_id + 10} value={x.menu_id}>{x.menu_name}</option>)}
                        </select>
                        <div >
                        </div>
                        <div>
                            <Articles array_obj={array_obj} articles={articles.data} artarr={artarr} setartarr={setartarr} />
                        </div>
                    </div>

                </div>

            </form>
        </div>
    )
}
export default Menuinsert;