import React, { useState, useEffect } from 'react';
import { getMenu, array_obj, getMenuAdminId, initalBaseUrl, deleteMenu, getMenuArtId } from '../actions';
import { Link } from 'react-router-dom';
import Updatelist from '../components/Updatelist';
import Inserticon from '../components/Inserticon';
function Main(props) {
    const [menu, setmenu] = useState({ data: [{}], status: 0 });
    const [menuid, setmenuid] = useState({ data: [{}], status: 0 });
    const [deletem, setdeletem] = useState({ del: 'nodelete', id1: 0, id2: 0 });
    const [deleteicon, setdeleteicon] = useState({ id: 0, bol: false });
    const [articlesid, setarticlesid] = useState({ data: [{}], status: 0 });
    useEffect(() => {
        getMenu(setmenu);

    }, []);
    useEffect(() => {
        getMenuAdminId(setmenuid, props.match.params.id);
        getMenuArtId(setarticlesid, props.match.params.id)
    }, [props.match.params.id]);

    useEffect(() => {
        deleteMenu(deletem.del, deletem.id1, deletem.id2);
    }, [deletem.del, deletem.id1, deletem.id2]);
    return (
        <div className="container-fluid" >
            <div className="row">
                <div className="col-sm">
                    <table className="table table-light">
                        <thead className="thead-light">

                            <tr>
                                <th style={{ width: '100px' }}></th>
                                <th colSpan="5">
                                    <div className="row">
                                        <div className="col-2">
                                            <h3>Меню</h3>
                                        </div>
                                        
                                        <Inserticon alias={`/menuinsert`} title={'Добавить пункт меню'}/>
                                    </div>
                                </th>
                                <th>
                                </th>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                array_obj(menu.data).map((x) =>

                                    <tr key={x.menu_id + 5}>
                                        <td className="form-check text-center" > <input id="my-input" className="form-check-input" type="checkbox" value={x.menu_id} onChange={(e) => setdeleteicon({ id: e.target.value, bol: e.target.checked })} /></td>
                                        <td><Link to={`/admin/index.php/menu/${x.menu_art_id}`}>{(x.parent === '0') ? x.menu_name : `- ${x.menu_name}`}</Link></td>
                                        <td>
                                            {(deleteicon.bol === true && x.menu_id === deleteicon.id) ?
                                                <div title="Удалить" style={{ cursor: 'pointer' }} onClick={(e) => { setdeletem({ del: 'delete', id1: x.menu_id, id2: x.menu_art_id }); document.location = `/menu/${menu.data[0].menu_art_id}` }}>
                                                    <svg className="bi bi-x-circle-fill" width="1em" height="1em" viewBox="0 0 16 16" fill="red" xmlns="http://www.w3.org/2000/svg">
                                                        <path fillRule="evenodd" d="M16 8A8 8 0 110 8a8 8 0 0116 0zm-4.146-3.146a.5.5 0 00-.708-.708L8 7.293 4.854 4.146a.5.5 0 10-.708.708L7.293 8l-3.147 3.146a.5.5 0 00.708.708L8 8.707l3.146 3.147a.5.5 0 00.708-.708L8.707 8l3.147-3.146z" clipRule="evenodd" />
                                                    </svg>
                                                </div> : ''}
                                        </td>
                                    </tr>
                                )
                            }
                        </tbody>

                    </table>
                </div>
                <div className="col">
                    <Updatelist
                        updatealias={`/admin/index.php/menuupdate/${menuid.data.menu_art_id}`}
                        name={menuid.data.menu_name}
                        alias={menuid.data.menu_alias}
                        title={menuid.data.menu_title}
                        keyword={menuid.data.menu_keyword}
                        content={menuid.data.menu_content}
                        img={menuid.data.menu_img}
                        initalBaseUrl={initalBaseUrl}
                    />


                    <div className="col-3">
                        Статьи
                        </div>
                    <div className="col">
                        {articlesid.data.map((x) =>
                            <div key={x.art_id + 25}>{x.art_name}</div>
                        )}
                    </div>
                </div>
            </div>
        </div>


    )
}
export default Main;