import React, { useState, useEffect } from 'react';
import { initalBaseUrl, getFiles, array_obj, getMenu, getArticles, insertArticles } from '../actions';
import { Link } from 'react-router-dom';
import Images from '../components/Images';

function Articlesinsert() {
    const [err, seterr] = useState({ data: '', status: 0 });
    const [files, setfiles] = useState({ data: {}, status: 0 });
    const [articles, setarticles] = useState({ data: [{}], status: 0 });
    const [names, setnames] = useState('');
    const [alias, setalias] = useState('');
    const [title, settitle] = useState('');
    const [keyword, setkeyword] = useState('');
    const [descript, setdescript] = useState('');
    const [content, setcontent] = useState('');
    const [img, setimg] = useState({ img: '', bol: false });
    const [params, setparams] = useState(0);
    const [subcontent, setsubcontent] = useState('');


    useEffect(() => {
        getFiles(setfiles);
        getArticles(setarticles);
    }, []);
    
    


    function handleSubmit(event) {
        insertArticles(seterr,img.img,  names, alias, title, keyword, descript, subcontent, content,  params);
        event.preventDefault();


    }

    return (
        <div className="container-fluid" >
            <h2>Создание нового пункта меню</h2>
            
            <form onSubmit={handleSubmit}>
                <div className="row">
                    <div className="row">
                        <button className="btn btn-primary ml-4 " type="submit">Сохранить</button>
                        <Link className="btn btn-primary ml-3" to={`/admin/index.php/articles/${articles.data[0].art_id}`}>Закрыть</Link>
                        <div className = "ml-3 err" >{err.data}</div>
                    </div>
                    
                </div>

                <div className="row">
                    <div className="col">
                        <div className="form-group">
                            <label htmlFor="menu_name"><h3>Название *</h3></label>
                            <input type="text" className="form-control" id="menu_name" onChange={(e) => setnames(e.target.value)} />
                        </div>
                    </div>
                    <div className="col">
                        <div className="form-group">
                            <label htmlFor="menu_alias"><h3>Адрес страницы *</h3></label>
                            <input type="text" onChange={(e) => setalias(e.target.value)} className="form-control" id="menu_alias" />
                        </div>
                    </div>
                    <div className="col">
                        <div className="form-group">
                            <label htmlFor="menu_alias"><h3>Заголовок страницы *</h3></label>
                            <input type="text" onChange={(e) => settitle(e.target.value)} className="form-control" id="menu_alias" />
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="col-sm">
                        <label htmlFor="menu_keyword"><h3>Ключевые слова</h3></label>
                        <textarea className="form-control" id="menu_keyword" onChange={(e) => setkeyword(e.target.value)} rows={3} defaultValue={""} />
                        <label htmlFor="menu_descript"><h3>Краткое описание страницы</h3></label>
                        <textarea className="form-control" id="menu_descript" onChange={(e) => setdescript(e.target.value)} rows={3} defaultValue={""} />
                        <label htmlFor="sub_content"><h3>Краткое описание статьи</h3></label>
                        <textarea className="form-control" id="sub_content" onChange={(e) => setsubcontent(e.target.value)} rows={3} defaultValue={""} />
                        <label htmlFor="content"><h3>Текст статьи</h3></label>
                        <textarea className="form-control" id="content" onChange={(e) => setcontent(e.target.value)} rows={10} defaultValue={""} />

                        <Images array_obj={array_obj} setimg={setimg} img={img} initalBaseUrl={initalBaseUrl} files={files.data} />
                    </div>
                   

                </div>

            </form>
        </div>
    )
}
export default Articlesinsert;