import React from 'react';
import { Link } from 'react-router-dom';
function Inserticon(props) {

    return (

        <div className="col-2 text-center" title={props.title}>
            <Link to={props.alias} className = "insert">
                
                <svg className="bi bi-plus-circle-fill" width="2em" height="2em" viewBox="0 0 16 16" fill="#007bff" xmlns="http://www.w3.org/2000/svg">
                    <path fillRule="evenodd" d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zM8.5 4a.5.5 0 0 0-1 0v3.5H4a.5.5 0 0 0 0 1h3.5V12a.5.5 0 0 0 1 0V8.5H12a.5.5 0 0 0 0-1H8.5V4z" />
                   
                </svg>
                
            </Link>
        </div>

    )
} export default Inserticon;