import React from 'react';
import { Link } from 'react-router-dom';
function Deleteicon(props) {

    return (
        <div className="row col" >
            <div className="col-4" title={props.title} onClick={() => props.deleteArticles(props.setartdelete, props.artarrsd)}>
                <Link to="#" className="delete">
                    <svg style={{ cursor: 'pointer' }} className="bi bi-x-circle-fill " width="2em" height="2em" viewBox="0 0 16 16" fill="red" xmlns="http://www.w3.org/2000/svg">
                        <path fillRule="evenodd" d="M16 8A8 8 0 110 8a8 8 0 0116 0zm-4.146-3.146a.5.5 0 00-.708-.708L8 7.293 4.854 4.146a.5.5 0 10-.708.708L7.293 8l-3.147 3.146a.5.5 0 00.708.708L8 8.707l3.146 3.147a.5.5 0 00.708-.708L8.707 8l3.147-3.146z" clipRule="evenodd" />
                    </svg>
                </Link>

            </div>
            <div className="col">
                {(props.artdelete.data.art_id !== '' && props.artdelete.status === 200) ? <div className="col err">{props.ok}</div> : ''}
                {(props.artdelete.data.art_id === "" && props.artdelete.status === 200) ? <div className="col err">{props.no}</div> : ''}

            </div>

        </div>
    )
} export default Deleteicon;