import React from 'react';
import axois from 'axios';
import { NavLink } from "react-router-dom";


export const initalBaseUrl = 'http://sandani.local';

export const array_obj = (arr) => {
    let new_arr = [];
    // eslint-disable-next-line
    for (const key in arr) {

        new_arr[key] = arr[key];


    }
    return new_arr;
}

export function getMenu(f) {
    axois.get(initalBaseUrl + '/api/admin/menu.php')
        .then(function (response) {
            f({ data: response.data, status: response.status });
        })
}

export function getMenuAdminId(f, id) {
    axois.get(initalBaseUrl + '/api/admin/menuid.php?id=' + id)
        .then(function (response) {
            f({ data: response.data, status: response.status });
        })
}
export function insertMenu(f, save, menuartid, nemes, alias, title, keyword, descript, content, img, parent) {
    axois.get(`${initalBaseUrl}/api/admin/insertmenu.php?menu_save=${save}&menu_art_id=${menuartid}&menu_name=${nemes}&menu_alias=${alias}&menu_title=${title}&menu_keyword=${keyword}&menu_descript=${descript}&menu_content=${content}&menu_img=${img}&parent=${parent}`)
        .then(function (response) {
            f({ data: response.data, status: response.status });
        })
}

