import React from 'react';

function Articles(props) {
   
    return (
        <div className="col-sm mt-3">
            <h3>Привязать статью</h3>
            {props.array_obj(props.articles).map((x, i) =>
                <div key={i + 10} className="form-check m-2 pt-2" >
                    <input id={`art_${i}`} value={x.art_id} className="form-check-input"  onChange={(e) => props.setartarr({id:e.target.value, c:e.target.checked})} type="checkbox" />
                    <label htmlFor={`art_${i}`} className="form-check-label">{x.art_name}</label>
                </div>
            )}
        </div>
    )
} export default Articles;