import React, { useState, useEffect } from 'react';
import { updateMenu, initalBaseUrl, getFiles, array_obj, getMenu, getMenuAdminId, getArticles, getMenuArtId, deleteMenuArt, insertMenuArt } from '../actions';
import { Link } from 'react-router-dom';
import Articles from '../components/Articles';
function Menuupdate(props) {
    const [menuname, setmenuname] = useState({ data: [{}], status: 0 });
    const [menu, setmenu] = useState({ data: [{}], status: 0 });
    const [files, setfiles] = useState({ data: {}, status: 0 });
    const [menuid, setmenuid] = useState({ data: [{}], status: 0 });
    const [articles, setarticles] = useState({ data: {}, status: 0 });
    const [id, setid] = useState(0);
    const [names, setnames] = useState('');
    const [alias, setalias] = useState('');
    const [title, settitle] = useState('');
    const [keyword, setkeyword] = useState('');
    const [descript, setdescript] = useState('');
    const [content, setcontent] = useState('');
    const [img, setimg] = useState({ img: '', bol: false });
    const [parent, setparent] = useState(0);
    const [artarr, setartarr] = useState({ id: 0, c: false });
    const [deletemenuarts, setdeletemenuarts] = useState(0);
    const [articlesid, setarticlesid] = useState({ data: [{}], status: 0 });
    const [artarrsd, setartarrsd] = useState([]);
    const [insermenuarticles, setinsermenuarticles] = useState({ data: {}, status: 0 });

    useEffect(() => {
        deleteMenuArt(deletemenuarts);
    }, [deletemenuarts]);

    useEffect(() => {
        getMenuAdminId(setmenuid, props.match.params.id);
        getMenuArtId(setarticlesid, props.match.params.id);
    }, [props.match.params.id]);

    function rtcheced(a) {
        let f = [];
        for (let i = 0; i < a.length; i++) {
            f[i] = a[i].art_id

        }
        return f
    }
    useEffect(() => {
        getFiles(setfiles);
        getMenu(setmenuname);
        getArticles(setarticles);

    }, []);
    useEffect(() => {
        setartarrsd(rtcheced(articlesid))
    }, [articlesid]);
    useEffect(() => {
        const option = artarrsd;
        if (artarr.c === true) {
            option.push(artarr.id);
            setartarrsd(option);
        }
        if (artarr.c === false) {
            var pos = option.indexOf(artarr.id);
            option.splice(pos, 1)
            setartarrsd(option);
        }
    }, [artarr.id, artarr.c]);

    useEffect(() => {
        setnames(menuid.data.menu_name);
        setalias(menuid.data.menu_alias);
        settitle(menuid.data.menu_title);
        setkeyword(menuid.data.menu_keyword);
        setdescript(menuid.data.menu_descript);
        setcontent(menuid.data.menu_content);
        setparent(menuid.data.parent);
        setid(menuid.data.menu_id);
        setimg({ img: menuid.data.menu_img, bol: false });

    }, [
        menuid.data.menu_name,
        menuid.data.menu_alias,
        menuid.data.menu_title,
        menuid.data.menu_keyword,
        menuid.data.menu_descript,
        menuid.data.menu_content,
        menuid.data.parent,
        menuid.data.menu_id,
        menuid.data.menu_img
    ]);

    function handleSubmit(event) {

        updateMenu(setmenu, id, props.match.params.id, names, alias, title, keyword, descript, content, img.img, parent);
        insertMenuArt(setinsermenuarticles, menuid.data.menu_art_id, artarrsd)

        event.preventDefault();


    }

    return (
        <div className="container-fluid" >
            <h2>Создание нового пункта меню</h2>
            <form onSubmit={handleSubmit}>
                <div className="row">
                    <div className="row">
                        <button className="btn btn-primary ml-4 " type="submit">Сохранить</button>
                        <Link className="btn btn-primary ml-3" to={`/admin/index.php/menu/${menuname.data[0].menu_art_id}`}>Закрыть</Link>
                    </div>

                </div>

                <div className="row">
                    <div className="col">
                        <div className="form-group">
                            <label htmlFor="menu_name"><h3>Название *</h3></label>
                            <input defaultValue={menuid.data.menu_name} type="text" className="form-control" id="menu_name" onChange={(e) => setnames(e.target.value)} />
                        </div>
                    </div>

                    <div className="col">
                        <div className="form-group">
                            <label htmlFor="menu_alias"><h3>Адрес страницы *</h3></label>
                            <input type="text" defaultValue={menuid.data.menu_alias} onChange={(e) => setalias(e.target.value)} className="form-control" id="menu_alias" />
                        </div>
                    </div>
                    <div className="col">
                        <div className="form-group">
                            <label htmlFor="menu_alias"><h3>Заголовок страницы *</h3></label>
                            <input type="text" defaultValue={menuid.data.menu_title} onChange={(e) => settitle(e.target.value)} className="form-control" id="menu_alias" />
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="col-sm">
                        <label htmlFor="menu_keyword"><h3>Ключевые слова</h3></label>
                        <textarea className="form-control" id="menu_keyword" onChange={(e) => setkeyword(e.target.value)} rows={3} defaultValue={menuid.data.menu_keyword} />
                        <label htmlFor="menu_descript"><h3>Краткое описание</h3></label>
                        <textarea className="form-control" id="menu_descript" onChange={(e) => setdescript(e.target.value)} rows={3} defaultValue={menuid.data.menu_descript} />
                        
                        <h3 className="mt-4">Изображение</h3>
                        <div className="col-sm-4"><img width="100%" src={`${initalBaseUrl}/img/window/${menuid.data.menu_img}`} alt="Изображение" /></div>
                        <div className="row">
                            {array_obj(files.data).map((x, i) =>
                                <div key={i + 10} style={{ border: 'solid 2px green' }} style={(img.img === x) ? { border: 'solid 2px red' } : { border: 'solid 2px green' }} className="form-check m-2  col-sm-2 " >
                                    <input style={{ marginLeft: '-1rem' }} id={`menu_img_${i}`} className="form-check-input" type="checkbox" value={(img.bol === false) ? x : ''} onChange={(e) => setimg({ img: e.target.value, bol: e.target.checked })} />
                                    <label htmlFor={`menu_img_${i}`} className="form-check-label"><img width="100%" src={`${initalBaseUrl}/img/window/${x}`} /></label>
                                </div>
                            )}


                        </div>
                    </div>
                    <div className="col-4">
                        <label htmlFor="menu_keyword"><h3>Привязать к пункту меню</h3></label>
                        <select className="form-control form-control-lg" onChange={(e) => setparent(e.target.value)}>
                            {array_obj(menuname.data).map((x) => (x.menu_id === menuid.data.parent) ? <option key={x.menu_id + 67} value={x.menu_id}>{x.menu_name}</option> : '')}

                            {array_obj(menuname.data).map((x) => <option key={x.menu_id + 64} value={x.menu_id}>{x.menu_name}</option>)}
                            <option value value="0">Нет</option>
                        </select>
                        <table className="col-sm">
                            <tr>
                                <td><h3 className="mt-3">Статьи</h3></td>
                            </tr>
                            {articlesid.data.map((x, i) =>
                                <tr key={i + 10}>
                                    <td className="pt-3">{x.art_name}</td>
                                    <td>
                                        <div title="Удалить" style={{ cursor: 'pointer' }} onClick={() => { setdeletemenuarts(x.menu_art_id); document.location = '/menuupdate/' + menuid.data.menu_art_id }}>
                                            <svg className="bi bi-x-circle-fill" width="1em" height="1em" viewBox="0 0 16 16" fill="red" xmlns="http://www.w3.org/2000/svg">
                                                <path fillRule="evenodd" d="M16 8A8 8 0 110 8a8 8 0 0116 0zm-4.146-3.146a.5.5 0 00-.708-.708L8 7.293 4.854 4.146a.5.5 0 10-.708.708L7.293 8l-3.147 3.146a.5.5 0 00.708.708L8 8.707l3.146 3.147a.5.5 0 00.708-.708L8.707 8l3.147-3.146z" clipRule="evenodd" />
                                            </svg>
                                        </div>
                                    </td>
                                </tr>

                            )}

                        </table>
                        <Articles array_obj={array_obj} articles={articles.data} artarr={artarr} setartarr={setartarr} />
                    </div>
                </div>


                <div>
                    <input type="hidden" defaultValue={menuid.data.menu_id} />
                </div>
            </form>
        </div>
    )
}
export default Menuupdate;